@charset "UTF-8";
*::-webkit-scrollbar {
  height: 6px;
  width: 6px;
  margin-right: 5px;
  background: #f5f5f5;
  transition: all 0.3s ease-in-out;
  border-radius: 0px;
}
*::-webkit-scrollbar-track {
  -webkit-border-radius: 0px;
  border-radius: 0px;
}
*::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0px;
  border-radius: 0px;
  background: rgba(0, 0, 0, 0.5);
}
*::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.6);
}
*::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.8);
}
*::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.4);
}

.page-header {
  height: 35px;
  background: transparent;
}
.page-header .actions {
  right: 20px;
}
.page-header .actions button {
  background: #000;
  color: #fff;
  line-height: 30px;
  padding: 0 8px;
  margin: 0;
  border: none;
}
.page-header .actions button.active {
  background: #888;
}

.checkbox {
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-size: inherit;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  text-align: center;
}
.checkbox:before {
  content: "";
}
.checkbox.checked:before {
  content: "";
}

.table-header {
  background: #333;
  color: #fff;
  line-height: 40px;
}
.table-header span {
  line-height: 40px;
}
.table-header .btn {
  padding: 0 10px;
  vertical-align: top;
  height: 40px;
}

.table-body .file-row,
.table-body .share-file-row {
  line-height: 30px;
}
.table-body .file-row .row-tools,
.table-body .share-file-row .row-tools {
  display: none;
}
.table-body .file-row .share_time .hover-hide,
.table-body .share-file-row .share_time .hover-hide {
  font-size: 12px;
}
.table-body .file-row:hover,
.table-body .share-file-row:hover {
  background: rgba(0, 0, 0, 0.1);
}
.table-body .file-row:hover .row-tools,
.table-body .share-file-row:hover .row-tools {
  display: block;
}
.table-body .file-row:hover .hover-hide,
.table-body .share-file-row:hover .hover-hide {
  display: none;
}
.table-body .file-row .row-edit,
.table-body .share-file-row .row-edit {
  display: none;
  width: 80%;
}
.table-body .file-row .row-edit input,
.table-body .share-file-row .row-edit input {
  height: 30px;
  width: 80%;
}
.table-body .file-row .row-editable .row-name,
.table-body .share-file-row .row-editable .row-name {
  display: none;
}
.table-body .file-row .row-editable .row-edit,
.table-body .share-file-row .row-editable .row-edit {
  display: inline-block;
}

.grid-box .row {
  margin: 0;
}
.grid-box .col-md-2 {
  box-sizing: border-box;
}
.grid-box .col-md-2 .file-icon {
  position: relative;
  text-align: center;
}
.grid-box .col-md-2 .file-icon .checkbox {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
.grid-box .col-md-2 .file-icon .fa {
  font-size: 60px;
  line-height: 90px;
}
.grid-box .col-md-2 .row-name {
  display: block;
  padding: 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
  text-align: center;
}
.grid-box .col-md-2.active, .grid-box .col-md-2:hover {
  border: 1px solid;
}
.grid-box .col-md-2.active .checkbox, .grid-box .col-md-2:hover .checkbox {
  display: block;
}

.crumb-box .col-xs-2 {
  text-align: right;
  padding: 8px 15px;
}

.dropdown {
  position: relative;
  background: #333;
  color: #fff;
  display: inline-block;
}
.dropdown .dropdown-menu {
  display: none;
  position: absolute;
  background: #888;
  color: #fff;
  width: 100%;
  z-index: 99;
  text-align: center;
  line-height: 30px;
}
.dropdown .dropdown-menu li {
  list-style: none;
}
.dropdown .dropdown-menu li a {
  color: #fff;
  text-decoration: none;
}
.dropdown .dropdown-menu li:hover {
  background: #333;
}
.dropdown:hover .dropdown-menu {
  display: block;
}

.dialog.dialog-box .dialog-body.zd-tab {
  padding: 0;
}
.dialog.dialog-box .dialog-body.zd-tab .zd-tab-body {
  margin-top: 10px;
}
.dialog.dialog-box .dialog-body.zd-tab .zd-tab-body .form-control {
  font-size: 20px;
  width: 100%;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #333;
  padding: 5px;
}
.dialog.dialog-box .dialog-body .row {
  margin: 0;
}

#shareModal {
  width: 600px;
}

.dialog.dialog-box.upload-dialog {
  right: 0;
  bottom: 0;
  width: 50%;
}
.dialog.dialog-box.upload-dialog .dialog-body .file-box {
  max-height: 200px;
  overflow-y: auto;
}
.dialog.dialog-box.upload-dialog .dialog-body .file-box .row:hover {
  background: rgba(0, 0, 0, 0.1);
}
.dialog.dialog-box.upload-dialog.max {
  display: block;
}
.dialog.dialog-box.upload-dialog.min {
  height: 40px;
  width: 100px;
  display: block;
}

.tree-box .tree-item {
  display: block;
  white-space: nowrap;
  text-decoration: none;
}
.tree-box .tree-item > span {
  cursor: pointer;
  display: inline-block;
  zoom: 1;
  vertical-align: middle;
}
.tree-box .tree-item > span:first-child, .tree-box .tree-item > span:nth-child(2) {
  display: inline-block;
  width: 20px;
  font-family: "Font Awesome 5 Free";
  font-size: inherit;
  text-rendering: auto;
  font-style: normal;
  font-variant: normal;
}
.tree-box .tree-item > span:first-child:before {
  content: "";
}
.tree-box .tree-item > span:nth-child(2):before {
  content: "";
}
.tree-box .tree-item:hover {
  background: #f4f9fd;
  border-color: #e5f0fb;
}
.tree-box li {
  list-style: none;
}
.tree-box li ul {
  display: none;
}
.tree-box li.open > ul {
  display: block;
}
.tree-box li.open > .tree-item > span:first-child:before {
  content: "";
}
.tree-box li.open > .tree-item > span:nth-child(2):before {
  content: "";
}
.tree-box li.open > .tree-item.empty > span:first-child:before {
  content: " ";
}
.tree-box li.open > .tree-item.empty > span:nth-child(2):before {
  content: "";
}
.tree-box li.active > .tree-item {
  background: #e5f0fb;
  border-color: #BBD4EF #fff;
}

.app-box {
  text-align: center;
}
.app-box .app-icon img {
  width: 6.25rem;
  width: 6.25rem;
}
.app-box .app-name {
  font-size: 1.875rem;
  margin: 0.625rem;
}
.app-box .app-url {
  display: block;
  background: #333;
  color: #fff;
  padding: 0.625rem 1.875rem;
  width: 12.5rem;
  margin: 0 auto;
}
.app-box .app-qr img {
  width: 12.5rem;
  height: 12.5rem;
}

.zd_listbox {
  height: 225px;
  width: 100%;
  overflow: auto;
  margin: 0;
  padding: 10px;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  border: 1px solid #ccc;
}
.zd_listbox li {
  list-style: none;
  line-height: 20px;
  text-align: center;
  cursor: pointer;
}
.zd_listbox li:hover {
  background: rgba(200, 200, 200, 0.5);
}
.zd_listbox li.selected {
  background: #1b809e;
  color: #fff;
}

.list-group .list-group-item {
  display: grid;
  grid-template: 60px/60px 1fr;
  grid-gap: 10px;
  margin-bottom: 20px;
  background: #fff;
}
.list-group .list-group-item .avatar img {
  width: 60px;
  height: 60px;
}

.zd_tree .zd_tree_item {
  display: block;
  white-space: nowrap;
  text-decoration: none;
}
.zd_tree li {
  list-style: none;
}
.zd_tree li ul {
  display: none;
}
.zd_tree li.open > ul {
  display: block;
}
.zd_tree .zd_tree_item > span {
  cursor: pointer;
  display: inline-block;
  zoom: 1;
  vertical-align: middle;
}
.zd_tree .zd_tree_item > span:first-child, .zd_tree .zd_tree_item > span:nth-child(2) {
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-size: inherit;
  text-rendering: auto;
}
.zd_tree .zd_tree_item:hover {
  background: #f4f9fd;
  border-color: #e5f0fb;
}
.zd_tree .zd_tree_item > span:first-child {
  margin: 0 3px;
  width: 15px;
  height: 15px;
}
.zd_tree .zd_tree_item > span:first-child::before {
  content: "";
}
.zd_tree .zd_tree_item > span:nth-child(2) {
  width: 22px;
  height: 18px;
  margin-right: 5px;
}
.zd_tree .zd_tree_item > span:nth-child(2)::before {
  content: "";
}
.zd_tree li.open > .zd_tree_item > span:first-child::before {
  content: "";
}
.zd_tree li.open > .zd_tree_item > span:nth-child(2)::before {
  content: "";
}
.zd_tree li > .zd_tree_item.empty > span:first-child::before {
  content: "";
}
.zd_tree li > .zd_tree_item.empty > span:nth-child(2)::before {
  content: "";
}
.zd_tree li.active > .zd_tree_item {
  background: #e5f0fb;
  border-color: #BBD4EF #fff;
}

.share-page {
  display: grid;
  grid-template-columns: 1fr 98px;
}
.share-page .user-box {
  text-align: center;
}

.file-row {
  display: grid;
  grid-template-columns: 40px 1fr 100px 180px;
}

.file-edit-row {
  display: grid;
  grid-template-columns: 40px 200px 1fr;
}

.share-file-row {
  display: grid;
  grid-template-columns: 40px 1fr 180px 64px 64px 64px;
}

.password-box {
  max-width: 320px;
  margin: 20px auto;
  background: #fff;
}
.password-box .box-header {
  position: relative;
  padding-left: 74px;
  background-color: #333;
  color: #fff;
  line-height: 60px;
}
.password-box .box-header .avatar {
  position: absolute;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  left: 20px;
  top: 8px;
}
.password-box .box-header .avatar img {
  width: 100%;
  height: 100%;
}
.password-box .box-header .name strong {
  margin-right: 10px;
}
.password-box .box-body {
  padding: 20px 20px 40px;
}
.password-box .box-body button {
  height: 30px;
  width: 80px;
  background: #333;
  color: #fff;
  border: 0;
  outline: 0;
}
.password-box .box-body input {
  height: 30px;
  border: 0;
  outline: 0;
  background: #f4f4f4;
  padding: 0 10px;
}